    import appRadio from '../../basic-component/app-radio/app-radio.vue';

    export default {
        name: "app-shipping-address",
        components: {appRadio},
	    props: {
            item: {
                type: Object,
            },
            is_hide_default_btn: {
                type: Boolean,
                default: false,
            },
            destroy_url: {
                type: String,
                default: ''
            },
            is_refund_address: {
                type: Number,
                default: 0,
            },
            type: {
                type: Number,
                default: 0,
            },
            theme: Object
	    },
        methods: {
            changeDefault() {
                const self = this;
                self.$request({
                    url: self.$api.user.address_default,
                    method: 'POST',
                    data: {
                        id: self.item.id,
                        type: self.type,
                        is_default: self.item.is_default == 1 ? 0 : 1,
                    }
                }).then((info) => {
                    if (info.code === 0) {
                        self.$emit('handleAddress');
                    } else {
                        uni.showToast({title: info.msg, icon: 'none'})
                    }
                });
            },
            edit() {
                uni.navigateTo({
                    url: `/pages/address/address-edit?id=${this.item.id}&is_refund_address=${this.is_refund_address}&type=${this.type}`
                });
            },
            editStop(){
                this.$emit('handleStop', 'editStop');
            },
            destroy() {
                const self = this;
                uni.showModal({
                    content: '确定删除收货地址',
                    success: function (res) {
                        if (res.confirm) {
                            self.$request({
                                url: self.destroy_url,
                                method: 'POST',
                                data: {
                                    id: self.item.id,
                                }
                            }).then((info) => {
                                if (info.code === 0) {
                                    self.$emit('handleAddress', 'delete')
                                } else {
                                    uni.showToast({title: info.msg, icon: 'none'})
                                }
                            });
                        }
                    }
                });
            },
        }
    }
